import { CloseCircleOutlined, PlusOutlined } from "@ant-design/icons";
import { Input, InputRef, Tag, theme } from "antd";
import { useEffect, useRef, useState } from "react";


export default function Form() {
    const { token } = theme.useToken();
    const [tags, setTags] = useState(['Tag 1', 'Tag 2', 'Tag 3']);
    const [inputVisible, setInputVisible] = useState(false);
    const [inputValue, setInputValue] = useState('');
    const inputRef = useRef<InputRef>(null);

    useEffect(() => {
        if (inputVisible) {
            inputRef.current?.focus();
        }
    }, [inputVisible]);

    const handleClose = (removedTag: string) => {
        const newTags = tags.filter((tag) => tag !== removedTag);
        console.log(newTags);
        setTags(newTags);
    };

    const showInput = () => {
        setInputVisible(true);
    };

    const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setInputValue(e.target.value);
    };

    const handleInputConfirm = () => {
        if (inputValue && tags.indexOf(inputValue) === -1) {
            setTags([...tags, inputValue]);
        }
        setInputVisible(false);
        setInputValue('');
    };

    const forMap = (tag: string) => (
        <span key={tag} style={{ display: 'inline-block' }}>
            <Tag
                closable
                onClose={(e) => {
                    e.preventDefault();
                    handleClose(tag);
                }}
            >
                {tag}
            </Tag>
        </span>
    );

    const tagChild = tags.map(forMap);

    const tagPlusStyle: React.CSSProperties = {
        background: token.colorBgContainer,
        borderStyle: 'dashed',
    };

    return (
        <form className="max-w-md mx-auto p-4 bg-white shadow rounded">
            <h2 className="text-lg text-cyan-700 font-semibold mb-4">Создать задачу</h2>
            <input className="w-full mb-5 p-2 border rounded" placeholder="Название задачи" />
            <input type="textarea" className="w-full p-2 border rounded" placeholder="Описание" />
            <Tag closeIcon={<CloseCircleOutlined />} >
                Tag 2
            </Tag>
            {inputVisible ? (
                <Input
                    ref={inputRef}
                    type="text"
                    size="small"
                    style={{ width: 78 }}
                    value={inputValue}
                    onChange={handleInputChange}
                    onBlur={handleInputConfirm}
                    onPressEnter={handleInputConfirm}
                />
            ) : (
                <Tag onClick={showInput} style={tagPlusStyle}>
                    <PlusOutlined /> New Tag
                </Tag>
            )}
            <button className="mt-4 w-full bg-blue-500 text-white py-2 rounded">Отправить</button>
        </form>
    );
}
